<template>
  <div class="visitorTitle">
    <h2>Î∞©Î™ÖÎ°ù</h2>
    <el-button type="primary" @click="goToWritePage">Í∏ÄÏì∞Í∏∞</el-button>
  </div>
  <el-table :data="visitors" class="table-responsive" style="width: 100%">
    <el-table-column prop="index" label="Î≤àÌò∏" width="80">
      <template #default="{ $index }">
        {{ calculateIndex($index) }}
      </template>
    </el-table-column>
    <el-table-column label="Ï†úÎ™©">
      <template #default="{ row }">
        <span v-if="row.isPrivate">üîí</span>
        <a @click.prevent="handleClick(row)">{{ row.title }}</a>
      </template>
    </el-table-column>
    <el-table-column prop="user.name" label="ÏûëÏÑ±Ïûê" width="100" />
    <el-table-column
      prop="createdAt"
      label="ÏûëÏÑ±Ïùº"
      width="150"
      :formatter="formatDate"
    />
    <el-table-column prop="views" label="Ï°∞ÌöåÏàò" width="80" />
  </el-table>
  <Pagination
    :total-pages="meta.totalPages"
    :current-page="currentPage"
    @page-changed="changePage"
  />
</template>

<script>
import axios from "axios";
import Pagination from "../common/PaginationButton.vue";

export default {
  name: "PageVisitor",
  components: {
    Pagination,
  },
  data() {
    return {
      visitors: [],
      meta: {
        totalPages: 0,
        currentPage: 1,
        totalCount: 0,
      },
    };
  },
  methods: {
    // Î∞©Î™ÖÎ°ù Ï†úÎ™© ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏
    async handleClick(row) {
      if (row.isPrivate) {
        const password = prompt("ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
        if (password) {
          await this.visitorPasswordCheck(row.id, password);
        }
      } else {
        await this.visitorViewRequest(row.id);
        this.$router.push(`/visitor/${row.id}`);
      }
    },

    // api ÏöîÏ≤≠ : Ï°∞ÌöåÏàò
    async visitorViewRequest(visitorId) {
      try {
        await axios.post(
          `${process.env.VUE_APP_BACKEND_URL}/api/view/visitor/${visitorId}`
        );
      } catch (error) {
        console.error("Error sending view request", error);
      }
    },

    // api ÏöîÏ≤≠ : ÎπÑÎ∞ÄÎ≤àÌò∏ Ï≤¥ÌÅ¨
    async visitorPasswordCheck(visitorId, password) {
      try {
        await axios.post(
          `${process.env.VUE_APP_BACKEND_URL}/api/visitor/${visitorId}/password`,
          { password }
        );
        this.visitorViewRequest(visitorId);
        this.$router.push(`/visitor/${visitorId}`);
      } catch (error) {
        console.error("Error sending view request", error);
        alert("ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÌãÄÎ†∏ÏäµÎãàÎã§.");
      }
    },

    // ÎÇ†Ïßú ÏÑ§Ï†ï
    formatDate(row, column) {
      const date = new Date(row[column.property]);
      return date.toLocaleDateString();
    },

    // Î∞©Î™ÖÎ°ù Í∏ÄÏì∞Í∏∞ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
    goToWritePage() {
      this.$router.push("/visitor/write"); // Í∏ÄÏì∞Í∏∞ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
    },

    // api Ìò∏Ï∂ú : Î∞©Î™ÖÎ°ù ÎûúÎçîÎßÅ
    async fetchVisitors(page) {
      try {
        const response = await axios.get(
          `${process.env.VUE_APP_BACKEND_URL}/api/visitor?page=${page}`
        );
        this.visitors = response.data.visitors;
        this.meta = response.data.meta;
        this.totalCount = response.data.meta.totalCount;
      } catch (error) {
        console.error("Error fetching visitors", error);
      }
    },

    // ÌéòÏù¥ÏßÄ Î≥ÄÍ≤Ω
    changePage(page) {
      this.currentPage = page;
      this.fetchVisitors(page);
      this.$router.push({
        path: this.$route.path,
        query: { page: page.toString() },
      });
    },

    // Í≤åÏãúÍ∏Ä Î≤àÌò∏ index Í≥ÑÏÇ∞
    calculateIndex(index) {
      return this.totalCount - (this.currentPage - 1) * 15 - index;
    },
  },
  // Ïã§ÏãúÍ∞Ñ Î≥ÄÍ≤Ω Í∞êÏßÄ
  watch: {
    "$route.query.page": function (newPage) {
      this.currentPage = Number(newPage) || 1;
      this.fetchVisitors(this.currentPage);
    },
  },
  created() {
    const pageFromUrl = this.$route.query.page || 1;
    this.currentPage = Number(pageFromUrl);
    this.fetchVisitors(this.currentPage);
  },
};
</script>


<style scoped>
h2 {
  margin-bottom: 30px;
}
.pagination {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.visitorTitle {
  display: flex;
  justify-content: space-between;
}

a {
  color: blue;
  text-decoration: underline;
}

a:hover {
  cursor: pointer;
}
</style>